<template>
    <div class="admin-manager">
        <!-- 管理员表格 -->
         <el-table
            v-loading="listLoading"
            :data="list"
            element-loading-text="Loading"
            border
            fit
            highlight-current-row
            >
            <el-table-column align="center" label="管理员ID" width="150" >
                <template slot-scope="scope">
                {{ scope.row.adminId}}
                </template>
            </el-table-column>
            <el-table-column label="管理员密码"  align="center">
                <template slot-scope="scope">
                {{ scope.row.password }}
                </template>
            </el-table-column>
             <el-table-column label="管理员状态" width="150"  align="center">
                <template slot-scope="scope">
                    <span :style="{'color':scope.row.status?'#67C23A':'#F56C6C'}"> 
                        {{scope.row.status?'正常':'禁用'}}</span>                
                </template>
            </el-table-column>
            <el-table-column label="操作" width="300"  align="center">
                  <template slot-scope="scope">
                    <el-button
                    size="mini"
                    type="primary"
                    @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    <el-button
                    size="mini"
                    type="warning"
                    @click="handleBan(scope.$index, scope.row)">
                       {{scope.row.status?'封号':'解禁'}}
                    </el-button>
                </template>
            </el-table-column>
            </el-table>
         <!-- 管理员表格结束 -->

         <!-- 管理员编辑框 -->
         <el-dialog :title="showText" :visible.sync="dialogFormVisible">
            <el-form :model="editInfo">
                <el-form-item label="管理员Id" :label-width="formLabelWidth" >
                  <el-input v-model="editInfo.adminId" autocomplete="off" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="管理员密码" :label-width="formLabelWidth">
                <el-input v-model="editInfo.password" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="管理员状态" :label-width="formLabelWidth">
                     <el-radio v-model="editInfo.status" :label="true" >正常</el-radio>
                     <el-radio v-model="editInfo.status" :label="false" > 封禁</el-radio>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="updateadminInfo()">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data(){
        return{
            list: [
                {
                    adminId:'admin',
                    password:'123456',
                    status:true
                },
                {
                    adminId:'admin1',
                    password:'123456',
                    status:true
                },
            ],
            listLoading: false,
            dialogFormVisible:false,
            currentIndex:'',
            showText:'添加管理员',
            editInfo:{
               adminId:'',
               password:'',
               status:false
            },
            formLabelWidth: '120px'
        }
        
    },
    methods:{
        //处理解封禁
        handleBan(index,row)
        {
            console.log(index,row);
            const id=row.adminId;
            if(row.status)
            {
                    this.$confirm(`此操作将封禁管理员ID为"${id}"的账号, 是否继续?`, '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                    }).then(() => {
                    row.status=!row.status;   
                    this.$message({
                        type: 'success',
                        message: '封禁成功!'
                    });
                    }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消封禁'
                    });          
                    });
            }
            else
            {
                  this.$confirm(`此操作将解封管理员ID为"${id}"的账号, 是否继续?`, '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                    }).then(() => {
                    row.status=!row.status;   
                    this.$message({
                        type: 'success',
                        message: '解封成功!'
                    });
                    }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消解封'
                    });          
                    });
            }
                
        },
        handleEdit(index, row)
        {
            const tutoId=row.tutoId;
            console.log(tutoId);
            this.dialogFormVisible=true;
            this.$turn(this.editInfo,this.list[index])
            this.editInfo.status=row.status;
            this.showText='修改管理员信息';
            this.currentIndex=index;
            console.log(this.editInfo);
        },
        clearInfo()
        {
           this.editInfo.titleContent='';
           this.editInfo.titleNum='';
           this.editInfo.content='';
        },
        async handleDelete(index,row) {
            console.log(index,row);
            const id=row.adminId;
            this.$confirm(`此操作将永久删除管理员"${id}", 是否继续?`, '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).then(() => {
            this.list.splice(index,1);
            this.$message({
                type: 'success',
                message: '删除成功!'
            });
            }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消删除'
            });          
            });
      },
      //更新管理员信息
      updateadminInfo()
      {
          console.log(this.editInfo);
          this.$turn(this.list[this.currentIndex],this.editInfo);
          if(!this.editInfo.status)
          this.list[this.currentIndex].status=false;
         this.dialogFormVisible=false;
         this.$message({
            message:'更新管理员信息成功！',
            type:'success'
         })
      }
    }
}
</script>

<style scoped>
     .admin-manager
     {
         width: 100%;
         position: relative;
     }
    
</style>